body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  background-image: url("https://image.freepik.com/free-photo/old-book-open-dark-wood-background_1373-581.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#orientation {
  width: 100vw;
  margin-top: 45vh;
  margin-left: 25vw;
  font-size: 1em;
  text-align: center;
}

@media (orientation: portrait) {
  #orientation { display: block; }
  .interaction  { display: none; }
  }

@media (orientation: landscape) {
  #orientation  { display: none; }
  .interaction { display: block; }
}